import React from 'react';
import { Icon, Divider, Typography } from 'antd';
import { injectIntl } from 'react-intl';
import QueueAnim from 'rc-queue-anim';

import { config } from './../../../config';

const { Text } = Typography;

const Services = ({ messages }) => {
  const services = [
    messages.landing[config.default][2],
    messages.landing[config.default][3],
    messages.landing[config.default][4],
  ];
  return (
    <div id="ssnn" className="container-fluid container-mw-xl" style={{ marginBottom: 40 }}>
      <div className="row" style={{ marginTop: '-80px' }}>
        {services.map((service, i) => (
          <div className="col-xl-4" key={i.toString()}>
            <div className="box box-default mb-4">
              <div className="box-body">
                <div className="icon-card-v1 icon-card--center">
                  <div className="icon--plain">
                    <Icon type={service.icon} className="text-primary" />
                  </div>
                  <h4 className="icon-card__header">{service.text1}</h4>
                  <div className="icon-card__content" style={{ minHeight: 95 }}>
                    {service.text2}
                  </div>
                  <Divider style={{ margin: '10px 0' }} />
                  <div className="icon-card__content" style={{ fontSize: 12, minHeight: 85 }}>
                    <Text strong style={{ color: '#0368c6' }}>
                      {service.text3}
                    </Text>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

const Page = ({ intl: { messages } }) => (
  <section className="chapter">
    <QueueAnim type="bottom" className="ui-animate">
      <div key="2">
        <Services messages={messages} />
      </div>
    </QueueAnim>
  </section>
);

export default injectIntl(Page);
